<template>
    <MainLayout>
        <div class="static-page page-wrap">
            <div class="container">
                <Spinner v-if="getMarketingFrequencyLoadingState === 'LOADING'"></Spinner>
                <div class="row">
                    <div class="col-md-12">
                        <h2>প্রতিলিপি সংকলন আনসাবস্ক্রাইব করুন</h2>
                        <div class="page-content">
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-6">
                        <div class="form-check">
                           <input v-model="unSubscribeOption" id="radio-option-1" type="radio" name="newsletter-option" value="TOO_MANY_EMAILS">
                           <label class="form-check-label" for="radio-option-1">আমি একাধিক ই-মেল পাচ্ছি</label>
                       </div>
                       <div class="form-check">
                           <input v-model="unSubscribeOption" id="radio-option-2" type="radio" name="newsletter-option" value="CONTENT_IRRELEVANT">
                           <label class="form-check-label" for="radio-option-2">বিষয়বস্তু আমার জন্য প্রাসঙ্গিক নয়</label>
                       </div>
                       <div class="form-check">
                           <input v-model="unSubscribeOption" id="radio-option-3" type="radio" name="newsletter-option" value="NO_TIME_TO_READ">
                           <label class="form-check-label" for="radio-option-3">আমার পড়ার সময়ের অভাব</label>
                       </div>
                       <div>
                            <button type="button" @click="unSubscribeUser" :disabled="!unSubscribeOption"  class="btn btn-save">আনসাবস্ক্রাইব</button>
                       </div>
                    </div>
                    <div class="col-md-6">
                        <img style="width: 100%" src="data:image/jpeg;base64,">
                    </div>
                </div>
            </div>
        </div>
    </MainLayout>
</template>

<script>
import MainLayout from '@/layout/main-layout.vue';
import constants from '@/constants';
import Spinner from '@/components/Spinner.vue';
import { mapGetters, mapActions } from 'vuex'

export default {
    components: {
        MainLayout,
        Spinner
    },
    data() {
        return {
            unSubscribeOption: null,
        }
    },
    computed: {
        ...mapGetters("settingspage",["getMarketingFrequencyLoadingState"])
        
    },
    methods: {

        ...mapActions("settingspage",["updateMarketingNewsletter"]),

        unSubscribeUser() {
            const that = this;
            const {uuid} = this.$route.query;
            if(!uuid) {
                alert("Invalid URL");
                return;
            }
            this.updateMarketingNewsletter({uuid, newsletterUnsubscribeReason: this.unSubscribeOption,newsletterFrequency: "NEVER"})
        }
        
    },
    watch: {

        "getMarketingFrequencyLoadingState"(state) {
            if(state==="LOADING_SUCCESS") {
                alert('আপনি প্রতিলিপি সংকলন মেল আনসাবস্ক্রাইব করতে সফল হয়েছেন।')
                this.$router.push('/');
            }
            if(state==="LOADING_ERROR") {
                alert('আনসাবস্ক্রাইব ব্যার্থ। অনুগ্রহ করে পুনঃপ্রচেষ্টা করুন।');
            }
        }
        
    },
    created() {
        
    }
}
</script>

<style lang="scss" scoped>
.static-page {
    margin-top: 85px;
    text-align: left;
    min-height: 600px;
    @media screen and (max-width: 992px ) {
        margin-top: 65px;
    }
    h2 {
        font-size: 22px;
        font-weight: bold;
        text-align: left;
        border-left: 3px solid #d0021b;
        padding-left: 10px;
        margin: 10px 0;
        @media screen and (max-width: 992px ) {
            font-size: 18px;
        }
    }
    button {
       font-size: 14px;
       margin: 5px 4px 10px 0;
       float: left;
       &.btn-save {
           background: #d0021b;
           color: #fff;
       }
   }
   .form-check {
        padding: 5px 0px;
        label{
            padding-left: 5px;
        }
   }
}
</style>
<style>
p {
    word-break: break-word !important;
}
</style>



